export default class ColorPicker {
  constructor(frames, colors) {
    this.frames = frames;
    this.colors = colors;
  }

  getColor(e) {
    this.coords = this.frames.getCoordinats(e.clientX, e.clientY);
    this.ctx = this.frames.getCurrent().getContext('2d');
    const imageData = this.ctx.getImageData(this.coords.x, this.coords.y, 1, 1);
    this.color = {};
    for (let k = 0; k < imageData.data.length; k += 4) {
      this.color.r = imageData.data[k + 0];
      this.color.g = imageData.data[k + 1];
      this.color.b = imageData.data[k + 2];
    }
    if (e.which === 1) {
      this.colors.primary.value = this.colors.rgbToHex(this.color.r, this.color.g, this.color.b);
    } else if (e.which === 3) {
      this.colors.secondary.value = this.colors.rgbToHex(this.color.r, this.color.g, this.color.b);
    }
  }
}
