export default class Preview {
  constructor(frames, canvas) {
    this.canvas = canvas;
    this.isFull = false;
    this.valueInput = null;
    this.interval = null;
    this.frames = frames;
    this.preview = 256;
  }

  beginAnimation() {
    this.refreshAnimatedPreview = this.refreshAnimatedPreview.bind(this);
    this.inputRangeChange = this.inputRangeChange.bind(this);
    this.interval = setInterval(this.refreshAnimatedPreview, 1000);
    const inputRange = document.getElementById('fps');
    const fullScreen = document.getElementById('fullscreen');

    fullScreen.addEventListener('click', this.controlledFullScreen.bind(this));
    inputRange.addEventListener('change', () => {
      clearInterval(this.interval);
      const { value } = document.querySelector('#fps');
      this.inputRangeChange(value);
    });
  }

  inputRangeChange(value) {
    this.fps = 1000 / value;
    if (this.valueInput !== value) {
      clearInterval(this.interval);
    }
    this.valueInput = value;
    this.interval = setInterval(this.refreshAnimatedPreview, this.fps);
  }

  refreshAnimatedPreview() {
    this.frames.animIndex += 1;
    const canvas = document.querySelector('#preview-animation');
    const ctx = canvas.getContext('2d');
    ctx.fillStyle = 'white';
    ctx.fillRect(0, 0, 256, 256);
    if (this.frames.animIndex >= this.frames.framesArray.length) {
      this.frames.animIndex = 0;
    }
    ctx.drawImage(this.frames.framesArray[this.frames.animIndex], 0, 0, this.canvas.sizeOfCanvas(),
      this.canvas.sizeOfCanvas(), 0, 0, this.preview, this.preview);
  }

  controlledFullScreen() {
    if (this.isFull) {
      this.isFull = false;
      this.noFullWScreen();
    } else {
      this.isFull = true;
      this.goToFullScreen();
    }
  }

  noFullWScreen() {
    document.exitFullscreen();
    const canvas = document.querySelector('#preview-animation');
    canvas.setAttribute('width', '256');
    canvas.setAttribute('height', '256');
    this.preview = 256;
  }

  goToFullScreen() {
    const container = document.querySelector('.preview_container');
    const canvas = document.querySelector('#preview-animation');
    canvas.setAttribute('width', '1000');
    canvas.setAttribute('height', '1000');
    this.preview = 1000;
    container.requestFullscreen();
  }

  get getFPS() {
    return this.fps;
  }
}
