import { createCanvas } from 'canvas';

export default class RotateTool {
  constructor(frames, canvas) {
    this.frames = frames;
    this.canvasClass = canvas;
  }

  rotate() {
    this.canvas = this.frames.getCurrent();
    const ctx = this.canvas.getContext('2d');
    const canvas2 = createCanvas(this.canvasClass.sizeOfCanvas(), this.canvasClass.sizeOfCanvas());
    const ctx2 = canvas2.getContext('2d');

    ctx.save();
    ctx2.drawImage(this.canvas, 0, 0);
    ctx.translate(this.canvasClass.sizeOfCanvas() / 2, this.canvasClass.sizeOfCanvas() / 2);
    ctx.rotate((90 * Math.PI) / 180);
    ctx.drawImage(canvas2, -this.canvas.width / 2, -this.canvas.width / 2);
    ctx.restore();

    this.frames.createFramesList();
  }
}
