/* eslint-disable no-useless-return */
export default class Rectangle {
  constructor(frames, colors, canvas, thickness) {
    this.frames = frames;
    this.colors = colors;
    this.canvasClass = canvas;
    this.thickness = thickness;
    this.started = false;
    this.pixel = 10;
  }

  draw(x, y) {
    if (x < 0 || y < 0 || x > this.canvasClass.sizeOfCanvas()
      || y > this.canvasClass.sizeOfCanvas()) return;
    this.ctx.clearRect(0, 0, this.canvasClass.sizeOfCanvas(), this.canvasClass.sizeOfCanvas());
    if (this.imageData) {
      this.ctx.putImageData(this.imageData, 0, 0);
    }
    this.x = Math.min(x, this.coords.x);
    this.y = Math.min(y, this.coords.y);
    this.w = Math.abs(x - this.coords.x);
    this.h = Math.abs(y - this.coords.y);

    this.ctx.lineWidth = this.thickness.thickness;
    if (this.button === 0) {
      this.ctx.strokeStyle = this.colors.primary.value;
    } else {
      this.ctx.strokeStyle = this.colors.secondary.value;
    }
    if (!this.w || !this.h) {
      return;
    }
    this.ctx.strokeRect(this.x - (this.x / 10), this.y - (this.y / 10), this.w, this.h);
    this.frames.createFramesList();
  }

  mainCnavasMouseDown(e) {
    e.preventDefault();
    this.started = true;
    this.ctx = this.frames.getCurrent().getContext('2d');
    this.imageData = this.ctx.getImageData(0, 0, this.canvasClass.sizeOfCanvas(),
      this.canvasClass.sizeOfCanvas());
    this.button = e.button;
    const canvas = e.target;
    this.canvas = canvas;
    this.coords = this.frames.getCoordinats(e.clientX, e.clientY);
    this.mainCnavasMouseMove = this.mainCnavasMouseMove.bind(this);
    this.mainCnavasMouseUp = this.mainCnavasMouseUp.bind(this);

    canvas.addEventListener('mousemove', this.mainCnavasMouseMove);
    canvas.addEventListener('mouseup', this.mainCnavasMouseUp);
  }

  mainCnavasMouseMove(evt) {
    if (evt.which === 1 || evt.which === 3) {
      this.coordsMove = this.frames.getCoordinats(evt.clientX, evt.clientY);
      this.draw(this.coordsMove.x, this.coordsMove.y);
    }
  }

  mainCnavasMouseUp(event) {
    if (this.started) {
      event.preventDefault();
      this.mainCnavasMouseMove(event);
      this.imageData = this.ctx.getImageData(0, 0, this.canvasClass.sizeOfCanvas(),
        this.canvasClass.sizeOfCanvas());
      this.started = false;
      this.canvas.removeEventListener('mousemove', this.mainCnavasMouseMove);
      this.canvas.removeEventListener('mouseup', this.mainCnavasMouseUp);
    }
  }
}
