export default class BushThickness {
  constructor() {
    this.size = 0;
    this.defaultBushThickness = null;
    this.bushThickness = 10;
  }

  begin() {
    this.defaultBushThickness = document.querySelector('.thickness-1');
    const container = document.querySelector('.tools__size_container');
    container.addEventListener('click', this.getBushThickness.bind(this));
  }

  getBushThickness(e) {
    this.defaultBushThickness.classList.remove('select');
    if (this.prevThickness) {
      this.prevThickness.classList.remove('select');
    }
    const { target } = e;
    target.classList.add('select');
    this.prevThickness = target;
    if (target.classList.contains('thickness-1')) {
      this.bushThickness = 10;
    }
    if (target.classList.contains('thickness-2')) {
      this.bushThickness = 20;
    }
    if (target.classList.contains('thickness-3')) {
      this.bushThickness = 30;
    }
    if (target.classList.contains('thickness-4')) {
      this.bushThickness = 40;
    }
  }

  get thickness() {
    return this.bushThickness;
  }
}
