import PenTool from './pen';

export default class Lighten extends PenTool {
  draw(x, y) {
    if (x < 0 || y < 0 || x > this.canvasClass.sizeOfCanvas()
      || y > this.canvasClass.sizeOfCanvas()) return;
    this.ctx = this.frames.getCurrent().getContext('2d');
    this.imageData = this.ctx.getImageData(x - (x % this.pixel), y - (y % this.pixel),
      this.thickness.thickness, this.thickness.thickness);
    for (let k = 0; k < this.imageData.data.length; k += 4) {
      this.imageData.data[k + 3] = this.imageData.data[k + 3] - 15;
    }
    this.ctx.putImageData(this.imageData, x - (x % this.pixel), y - (y % this.pixel));
    this.frames.createFramesList();
  }
}
