/* eslint-disable */
export default class Frames {
  constructor(canvas, layer) {
    this.layer = layer;
    this.canvas = canvas;
    this.framesArray = [];
    this.layersOfFrame = null;
    this.index = -1;
    this.animIndex = 0;
    this.count = 1;
    this.context = null;
    this.number = null;
    this.canvasSize = document.getElementById('canvas-size');
    this.layersOfFrame = [];
  }

  getCoordinats(x, y) {
    const canvas = this.getCurrent();
    const canvasRect = canvas.getBoundingClientRect();
    return {
      x: x - canvasRect.left,
      y: y - canvasRect.top,
    };
  }

  beginFrames() {
    this.beginSessionStorage();
    const input = document.getElementById('sizeForCanvas');
    input.addEventListener('change', this.redraw.bind(this));
    const btn = document.querySelector('.frames__add_new_frame');
    btn.addEventListener('click', this.addFrame.bind(this));
    const save = document.querySelector('.preview__save_usersession');
    save.addEventListener('click', this.savelocalStorage.bind(this));
    this.addFrame();
  }

  beginSessionStorage() {
    if (sessionStorage.getItem('0')) {
      for (let i = 0; i < +sessionStorage.getItem('length'); i += 1) {
        const canvas = document.createElement('canvas');
        canvas.setAttribute('id', 'palete');
        canvas.setAttribute('width', `${this.canvas.sizeOfCanvas()}`);
        canvas.setAttribute('height', `${this.canvas.sizeOfCanvas()}`);
        const context = canvas.getContext('2d');
        const image = new Image();
        image.onload = () => {
          context.drawImage(image, 0, 0);
        };
        image.setAttribute('src', sessionStorage.getItem(`${i}`));
        this.framesArray.push(canvas);
      }
    }
  }

  redraw() {
    const canvas = this.framesArray[this.index];
    canvas.setAttribute('width', `${this.canvas.sizeOfCanvas()}`);
    canvas.setAttribute('height', `${this.canvas.sizeOfCanvas()}`);

    this.canvasSize.innerHTML = `${this.canvas.sizeOfCanvas() / 10} * ${this.canvas.sizeOfCanvas() / 10}`;

    const imageData = this.context.createImageData(this.canvas.sizeOfCanvas(),
      this.canvas.sizeOfCanvas());
    for (let k = 0; k < imageData.data.length; k += 4) {
      imageData.data[k + 0] = 0;
      imageData.data[k + 1] = 0;
      imageData.data[k + 2] = 0;
      imageData.data[k + 3] = 0;
    }
    this.context.putImageData(imageData, 0, 0);
    this.framesArray[this.index] = canvas;
  }

  getCurrent() {
    return this.framesArray[this.index];
  }

  addFrame() {
    const canvas = document.createElement('canvas');
    canvas.setAttribute('id', 'palete');
    canvas.setAttribute('width', `${this.canvas.sizeOfCanvas()}`);
    canvas.setAttribute('height', `${this.canvas.sizeOfCanvas()}`);
    canvas.addEventListener('contextmenu', (e) => {
      e.preventDefault();
      e.stopPropagation();
      e.cancelBubble = true;
      return false;
    });

    const context = canvas.getContext('2d');
    this.context = context;
    const imageData = this.context.createImageData(this.canvas.sizeOfCanvas(),
      this.canvas.sizeOfCanvas());
    for (let k = 0; k < imageData.data.length; k += 4) {
      imageData.data[k + 0] = 255;
      imageData.data[k + 1] = 255;
      imageData.data[k + 2] = 255;
      imageData.data[k + 3] = 255;
    }
    context.putImageData(imageData, 0, 0);
    this.number = 0;
    this.framesArray.push(canvas);
    this.setFrame(this.framesArray.length - 1);
  }

  setFrame(frameIndex) {
    this.index = frameIndex;
    document.querySelector('.main__canvas').innerHTML = '';
    document.querySelector('.main__canvas').appendChild(this.getCurrent());
    if (this.layer.currentLayer) {
      this.updateFrame(this.layer.currentLayer);
    }
    this.createFramesList();
  }

  updateFrame(currentLayer) {
    const imageData = currentLayer;
    for (let i = 0; i < currentLayer.data.length; i += 4) {
      if (imageData.data[i + 3] !== 0) {
        imageData.data[i + 3] = 255;
      }
    }
    const canvas = document.getElementById('palete');
    canvas.getContext('2d').putImageData(imageData, 0, 0);
    this.framesArray[this.index] = canvas;
  }

  createFramesList() {
    const list = document.querySelector('.frames__list');
    list.innerHTML = '';
    for (let i = 0; i < this.framesArray.length; i += 1) {
      this.layersOfFrame[i] = this.layer.layers;
      const frame = document.createElement('li');
      frame.classList.add('frames__list_item');
      const canvasFrame = document.createElement('canvas');
      canvasFrame.setAttribute('width', '128');
      canvasFrame.setAttribute('height', '128');

      canvasFrame.getContext('2d').drawImage(this.framesArray[i], 0, 0, this.canvas.sizeOfCanvas(), this.canvas.sizeOfCanvas(), 0, 0, 128, 128);

      const del = document.createElement('div');
      del.classList.add('frames__list_item__delete');
      frame.appendChild(del);

      const num = document.createElement('div');
      num.classList.add('frames__list_item__number');
      num.innerHTML = `${i + 1}`;
      frame.appendChild(num);

      const duplicate = document.createElement('div');
      duplicate.classList.add('frames__list_item__duplicate');
      frame.appendChild(duplicate);

      frame.appendChild(canvasFrame);
      list.appendChild(frame);

      const delArr = document.querySelectorAll('.frames__list_item__delete');
      this.deleteFrame = this.deleteFrame.bind(this);
      delArr[i].addEventListener('click', (e) => {
        this.deleteFrame(e, i);
      });

      const numArr = document.querySelectorAll('.frames__list_item__number');
      this.setFrame = this.setFrame.bind(this);
      numArr[i].addEventListener('click', () => {
        this.setFrame(i);
      });

      const duplicateArr = document.querySelectorAll('.frames__list_item__duplicate');
      this.duplicateFrame = this.duplicateFrame.bind(this);
      duplicateArr[i].addEventListener('click', (e) => {
        this.duplicateFrame(e, i);
      });
    }
  }
  savelocalStorage() {
    for (let i = 0; i < this.framesArray.length; i += 1) {
      const elementUrl = this.framesArray[i].toDataURL();
      sessionStorage.setItem(`${i}`, elementUrl);
    }
    sessionStorage.setItem('length', `${this.framesArray.length}`);
  }

  duplicateFrame(e, i) {
    this.animIndex -= 1;
    const { target } = e;
    const dupFrame = target.parentNode.cloneNode(true);

    const canvas = dupFrame.querySelector('canvas');
    canvas.setAttribute('width', '320');
    canvas.setAttribute('height', '320');
    const ctx = canvas.getContext('2d');
    ctx.fillStyle = 'white';
    ctx.fillRect(0, 0, 320, 320);

    if (this.framesArray[i]) {
      ctx.drawImage(this.framesArray[i], 0, 0, 320, 320, 0, 0, 320, 320);
    }
    this.framesArray.push(canvas);
    this.setFrame(this.framesArray.length - 1);
  }

  deleteFrame(e, i) {
    const { target } = e;
    target.parentNode.parentNode.removeChild(target.parentNode);
    this.framesArray.splice(i, 1);
    this.animIndex -= 1;
    this.setFrame(this.framesArray.length - 1);
  }
}
