/* eslint-disable yoda */
/* eslint-disable no-cond-assign */
export default class PaintBucket {
  constructor(frames, colors, canvas) {
    this.canvasArray = canvas;
    this.colors = colors;
    this.reachLeft = null;
    this.reachRight = null;
    this.pixelPos = null;
    this.coord = null;
    this.frames = frames;
    this.colorLayer = {};
    this.canvas = null;
    this.pixel = 10;
    this.x = 0;
    this.y = 0;
    this.startR = null;
    this.startG = null;
    this.startB = null;
    this.button = null;
    this.context = 0;
  }

  beginBucket(e) {
    e.preventDefault();
    this.button = e.button;
    const canvas = e.target;
    this.canvas = canvas;
    const coords = this.frames.getCoordinats(e.clientX, e.clientY);

    this.paint(coords.x, coords.y);
  }

  paint(xCoord, yCoord) {
    if (xCoord < 0 || yCoord < 0 || xCoord > this.canvasArray.sizeOfCanvas()
      || yCoord > this.canvasArray.sizeOfCanvas()) return;
    this.ctx = this.frames.getCurrent().getContext('2d');
    const xFloor = Math.floor(xCoord / 10) * 10;
    const yFloor = Math.floor(yCoord / 10) * 10;
    this.x = xFloor;
    this.y = yFloor;

    const imageData = this.ctx.getImageData(0, 0, this.canvasArray.sizeOfCanvas(),
      this.canvasArray.sizeOfCanvas());
    this.colorLayer = imageData;

    this.getBeginColor();

    const pixelStack = [[this.x, this.y]];
    const color = this.colors.getRgb(this.button);
    this.colorR = color.r;
    this.colorG = color.g;
    this.colorB = color.b;

    while (pixelStack.length) {
      const newCoord = pixelStack.pop();
      const x = newCoord[0];
      let y = newCoord[1];
      this.pixelPos = (y * this.canvasArray.sizeOfCanvas() + x) * 4;
      while ((y -= 1) >= 0 && this.matchStartColor(this.pixelPos)) {
        this.pixelPos -= this.canvasArray.sizeOfCanvas() * 4;
      }

      this.pixelPos += this.canvasArray.sizeOfCanvas() * 4;

      y += 1;
      this.reachLeft = false;
      this.reachRight = false;

      while ((y += 1) < this.canvasArray.sizeOfCanvas() - 1
        && this.matchStartColor(this.pixelPos)) {
        this.colorPixel();
        if (x > 0) {
          if (this.matchStartColor(this.pixelPos - 4)) {
            if (!this.reachLeft) {
              pixelStack.push([x - 1, y]);
              this.reachLeft = true;
            }
          } else if (this.reachLeft) {
            this.reachLeft = false;
          }
        }

        if (x < this.canvasArray.sizeOfCanvas() - 1) {
          if (this.matchStartColor(this.pixelPos + 4)) {
            if (!this.reachRight) {
              pixelStack.push([x + 1, y]);
              this.reachRight = true;
            }
          } else if (this.reachRight) {
            this.reachRight = false;
          }
        }
        this.pixelPos += this.canvasArray.sizeOfCanvas() * 4;
      }
    }

    this.ctx.putImageData(this.colorLayer, 0, 0);

    this.frames.createFramesList();
  }

  getBeginColor() {
    const imageData = this.ctx.getImageData(this.x, this.y, 10, 10);
    for (let k = 0; k < imageData.data.length; k += 4) {
      this.startR = imageData.data[k + 0];
      this.startG = imageData.data[k + 1];
      this.startB = imageData.data[k + 2];
      this.startA = imageData.data[k + 3];
    }
  }

  matchStartColor(pixelPos) {
    const r = this.colorLayer.data[pixelPos];
    const g = this.colorLayer.data[pixelPos + 1];
    const b = this.colorLayer.data[pixelPos + 2];
    const a = this.colorLayer.data[pixelPos + 3];

    return (r === this.startR && g === this.startG && b === this.startB && a === this.startA);
  }

  colorPixel() {
    this.color = this.colors.getRgb(this.button);
    this.colorLayer.data[this.pixelPos] = this.color.r;
    this.colorLayer.data[this.pixelPos + 1] = this.color.g;
    this.colorLayer.data[this.pixelPos + 2] = this.color.b;
    this.colorLayer.data[this.pixelPos + 3] = 255;
  }
}
