/* eslint-disable no-cond-assign */
import MoveTool from './MoveTool';

export default class ShapeSelection extends MoveTool {
  beginSelect(e) {
    this.coords = this.frames.getCoordinats(e.clientX, e.clientY);
    this.canvas = this.frames.getCurrent();
    this.ctx = this.frames.getCurrent().getContext('2d');
    this.select(this.coords.x, this.coords.y, e);
  }

  select(xCoord, yCooord) {
    if (this.number !== undefined) return;
    this.moveImageData = this.ctx.createImageData(this.canvasClass.sizeOfCanvas(),
      this.canvasClass.sizeOfCanvas());
    this.number = 1;
    const xFloor = Math.floor(xCoord / 10) * 10;
    const yFloor = Math.floor(yCooord / 10) * 10;
    if (xFloor < 0 || yFloor < 0 || xFloor > this.canvasClass.sizeOfCanvas()
      || yFloor > this.canvasClass.sizeOfCanvas()) return;
    this.ctx = this.frames.getCurrent().getContext('2d');
    this.x = xFloor;
    this.y = yFloor;

    const imageData = this.ctx.getImageData(0, 0, this.canvasClass.sizeOfCanvas(),
      this.canvasClass.sizeOfCanvas());
    this.colorLayer = imageData;

    this.getBeginColor();

    const pixelStack = [[this.x, this.y]];

    while (pixelStack.length) {
      const newCoord = pixelStack.pop();
      const x = newCoord[0];
      let y = newCoord[1];
      this.pixelPos = (y * this.canvasClass.sizeOfCanvas() + x) * 4;
      while ((y -= 1) >= 0 && this.matchStartColor(this.pixelPos)) {
        this.pixelPos -= this.canvasClass.sizeOfCanvas() * 4;
      }

      this.pixelPos += this.canvasClass.sizeOfCanvas() * 4;

      y += 1;
      this.reachLeft = false;
      this.reachRight = false;

      while ((y += 1) < this.canvasClass.sizeOfCanvas() - 1
        && this.matchStartColor(this.pixelPos)) {
        this.colorPixel();
        if (x > 0) {
          if (this.matchStartColor(this.pixelPos - 4)) {
            if (!this.reachLeft) {
              pixelStack.push([x - 1, y]);
              this.reachLeft = true;
            }
          } else if (this.reachLeft) {
            this.reachLeft = false;
          }
        }

        if (x < this.canvasClass.sizeOfCanvas() - 1) {
          if (this.matchStartColor(this.pixelPos + 4)) {
            if (!this.reachRight) {
              pixelStack.push([x + 1, y]);
              this.reachRight = true;
            }
          } else if (this.reachRight) {
            this.reachRight = false;
          }
        }
        this.pixelPos += this.canvasClass.sizeOfCanvas() * 4;
      }
    }

    this.ctx.putImageData(this.colorLayer, 0, 0);
    this.canvas.addEventListener('mousedown', this.mainCnavasMouseDown.bind(this));
    this.canvas.addEventListener('mouseout', this.mainCnavasMouseOut.bind(this));
    this.frames.createFramesList();
  }

  matchStartColor(pixelPos) {
    const r = this.colorLayer.data[pixelPos];
    const g = this.colorLayer.data[pixelPos + 1];
    const b = this.colorLayer.data[pixelPos + 2];
    const a = this.colorLayer.data[pixelPos + 3];

    return (r === this.startR && g === this.startG && b === this.startB && a === this.startA);
  }

  colorPixel() {
    this.colorLayer.data[this.pixelPos] = 0;
    this.colorLayer.data[this.pixelPos + 1] = 191;
    this.colorLayer.data[this.pixelPos + 2] = 255;
    this.colorLayer.data[this.pixelPos + 3] = 20;
    this.moveImageData.data[this.pixelPos] = this.colorLayer.data[this.pixelPos];
    this.moveImageData.data[this.pixelPos + 1] = this.colorLayer.data[this.pixelPos + 1];
    this.moveImageData.data[this.pixelPos + 2] = this.colorLayer.data[this.pixelPos + 2];
    this.moveImageData.data[this.pixelPos + 3] = this.colorLayer.data[this.pixelPos + 3];
  }

  getBeginColor() {
    const imageData = this.ctx.getImageData(this.x, this.y, 10, 10);
    for (let k = 0; k < imageData.data.length; k += 4) {
      this.startR = imageData.data[k + 0];
      this.startG = imageData.data[k + 1];
      this.startB = imageData.data[k + 2];
      this.startA = imageData.data[k + 3];
    }
  }

  mainCnavasMouseDown(e) {
    e.preventDefault();
    this.started = true;
    this.imageData = this.moveImageData;
    const canvas = e.target;
    this.canvas = canvas;
    this.mainCnavasMouseMove = this.mainCnavasMouseMove.bind(this);
    this.mainCnavasMouseUp = this.mainCnavasMouseUp.bind(this);
    this.startCoords = {
      x: e.pageX,
      y: e.pageY,
    };
    canvas.addEventListener('mousemove', this.mainCnavasMouseMove);
    canvas.addEventListener('mouseup', this.mainCnavasMouseUp);
  }

  mainCnavasMouseOut() {
    this.canvas.removeEventListener('mousemove', this.mainCnavasMouseMove);
    this.canvas.removeEventListener('mouseup', this.mainCnavasMouseUp);
  }

  mainCnavasMouseUp(event) {
    if (this.started) {
      event.preventDefault();
      this.mainCnavasMouseMove(event);
      this.imageData = this.ctx.getImageData(0, 0, this.canvasClass.sizeOfCanvas(),
        this.canvasClass.sizeOfCanvas());
      this.started = false;
      this.number = undefined;
    }
  }
}
