import Frame from './components/frames/frames';
import EventsForTools from './components/tools/eventForTools';
import Pen from './components/tools/pen';
import Bucket from './components/tools/PaintBucket';
import Preview from './screens/preview/Preview';
import Colors from './components/colors/Colors';
import OAUTH from './components/oauth/oauth';
import Gif from './screens/export/gif/exportAsGif';
import Layers from './components/layers/Layers';
import Canvas from './screens/canvas/Canvas';
import BushThickness from './components/tools/bushThickness';
import Coordinats from './screens/canvas/Coordinats';
import Png from './screens/export/png/exportAsPng';
import Rectangle from './components/tools/Rectamgle';
import MirrorPen from './components/tools/MirrorPen';
import Lighten from './components/tools/Lighten';
import ColorPicker from './components/tools/ColorPicker';
import Dithering from './components/tools/Dithering';
import PaintAllPixelsOfSameColor from './components/tools/PaintAllPixelsOfSameColor';
import StrokeLine from './components/tools/StrokeLine';
import MoveTool from './components/tools/MoveTool';
import ShapeSelection from './components/tools/ShapeSelection';
import KeyBoardShortcuts from './components/KeyBoardShortcuts/KeyBoardShortCuts';
import ModalWindow from './components/ModalWindow/ModalWindow';
import APNG from './screens/export/apng/apng';
import ModalWindowForExport from './screens/export/ModalWindowForExport';
import RotateTool from './components/tools/RotateTool';
import FlipTool from './components/tools/FlipVerticaly';
import Circle from './components/tools/Circle';
import './screens/index';

if (document.querySelector('.frames__add_new_frame') !== null) {
  const containerForTools = document.querySelector('.tools__draw');
  /* const btnNewLayer = document.querySelector('.layers__add_new_layer'); */

  const colors = new Colors();
  const png = new Png();
  const canvas = new Canvas();
  const layer = new Layers(canvas);
  const coordinats = new Coordinats();
  const thickness = new BushThickness();
  const frames = new Frame(canvas, layer);
  const penTool = new Pen(frames, colors, canvas, thickness);
  const move = new MoveTool(frames, canvas);
  const shapeSlection = new ShapeSelection(frames, canvas);
  const paintAllPixelsOfSameColor = new PaintAllPixelsOfSameColor(frames, colors, canvas);
  const lighten = new Lighten(frames, colors, canvas, thickness);
  const dithering = new Dithering(frames, colors, canvas, thickness);
  const colorPicker = new ColorPicker(frames, colors);
  const mirrorPen = new MirrorPen(frames, colors, canvas, thickness);
  const bucket = new Bucket(frames, colors, canvas);
  const rectangle = new Rectangle(frames, colors, canvas, thickness);
  const strokeLine = new StrokeLine(frames, colors, canvas, thickness);
  const rotate = new RotateTool(frames, canvas);
  const flip = new FlipTool(frames, canvas);
  const circle = new Circle(frames, colors, canvas, thickness);
  const eventForTools = new EventsForTools(penTool, bucket, rectangle,
    mirrorPen, lighten, colorPicker, dithering, paintAllPixelsOfSameColor,
    strokeLine, move, shapeSlection, rotate, flip, circle);
  const keyBoard = new KeyBoardShortcuts(eventForTools, thickness);
  const modalWindow = new ModalWindow(keyBoard);
  const preview = new Preview(frames, canvas);
  const gif = new Gif(frames, canvas, preview);
  const apng = new APNG(frames);
  const modalWindowForExport = new ModalWindowForExport(png, gif, apng);

  window.addEventListener('load', modalWindowForExport.addEvents.bind(modalWindowForExport));
  window.addEventListener('load', thickness.begin.bind(thickness));
  window.addEventListener('load', frames.beginFrames.bind(frames));
  window.addEventListener('load', preview.beginAnimation.bind(preview));
  containerForTools.addEventListener('click', eventForTools.beginToolsEvent.bind(eventForTools));
  window.addEventListener('load', keyBoard.keyToolsEvent.bind(keyBoard));
  window.addEventListener('load', layer.beginLayer.bind(layer));
  canvas.redrawCanvasForSize();
  window.addEventListener('load', coordinats.beginShowCoordinats.bind(coordinats));
  window.addEventListener('load', modalWindow.addEvents.bind(modalWindow));
}

const login = document.querySelector('.title__link_to_sign');
const logout = document.querySelector('#logoutText');

const oauth = new OAUTH();

login.addEventListener('click', oauth.login.bind(oauth));
logout.addEventListener('click', oauth.startLogOutPolling.bind(oauth));
