export default class ModalWindowForExport {
  constructor(png, gif, apng) {
    this.png = png;
    this.gif = gif;
    this.apng = apng;
  }

  addEvents() {
    this.gif.width = 320;
    const modal = document.querySelector('#modal-export');
    const modalOverlay = document.querySelector('#modal-overlay-export');
    const closeButton = document.querySelector('#close-button-export');
    const openButton = document.querySelector('#open-button-export');

    const btnForExportGif = document.querySelector('.export__gif');
    const btnForExportAPNG = document.querySelector('.export__apng');

    this.png.getLinlToDownload.call(this.png);
    btnForExportGif.addEventListener('click', this.gif.beginMakeGif.bind(this.gif));
    btnForExportAPNG.addEventListener('click', this.apng.beginMakeApng.bind(this.apng));

    closeButton.addEventListener('click', () => {
      modal.classList.toggle('opened');
      modalOverlay.classList.toggle('opened');
    });

    openButton.addEventListener('click', () => {
      modal.classList.toggle('opened');
      modalOverlay.classList.toggle('opened');
    });
  }
}
