import hotkeys from 'hotkeys-js';

export default class ModalWindow {
  constructor(keyBoard) {
    this.keyBoard = keyBoard;
  }

  addEvents() {
    this.keyBoard.keyToolsEvent.bind(this);
    const modal = document.querySelector('#modal');
    const modalOverlay = document.querySelector('#modal-overlay');
    const closeButton = document.querySelector('#close-button');
    const openButton = document.querySelector('#open-button');
    const container = document.querySelector('.modal-guts');

    container.addEventListener('click', this.changeKeyClick.bind(this));

    closeButton.addEventListener('click', () => {
      modal.classList.toggle('opened');
      modalOverlay.classList.toggle('opened');
    });

    openButton.addEventListener('click', () => {
      modal.classList.toggle('opened');
      modalOverlay.classList.toggle('opened');
    });
  }

  changeKeyClick(e) {
    this.target = e.target;
    this.linkOfchangeKey = this.changeKey.bind(this);
    window.addEventListener('keyup', this.linkOfchangeKey);
  }

  changeKey() {
    if (this.target.classList.contains('tools__draw__pen')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyPen = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyPen}`);
      });
    }
    if (this.target.classList.contains('tools__draw__bucket')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyBucket = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyBucket}`);
      });
    }
    if (this.target.classList.contains('tools__draw__erasier')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyEraiser = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyEraiser}`);
      });
    }
    if (this.target.classList.contains('tools__draw__rectangle')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyRectangle = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyRectangle}`);
      });
    }
    if (this.target.classList.contains('tools__draw__mirror_pen')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyMirror = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyMirror}`);
      });
    }
    if (this.target.classList.contains('tools__draw__lighten')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyLighten = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyLighten}`);
      });
    }
    if (this.target.classList.contains('tools__draw__color_picker')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyColorPicker = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyColorPicker}`);
      });
    }
    if (this.target.classList.contains('tools__draw__dithering')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyDithering = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyDithering}`);
      });
    }
    if (this.target.classList.contains('tools__draw__paint_all_pixels')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyPaint = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyPaint}`);
      });
    }
    if (this.target.classList.contains('tools__draw__stroke_line')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyStroke = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyStroke}`);
      });
    }
    if (this.target.classList.contains('tools__draw__move_tool')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyMove = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyMove}`);
      });
    }
    if (this.target.classList.contains('tools__draw__shape_tool')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyShape = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyShape}`);
      });
    }
    if (this.target.classList.contains('tools__draw__flip_tool')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyFlip = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyFlip}`);
      });
    }
    if (this.target.classList.contains('tools__draw__rotate_tool')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyRotate = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyRotate}`);
      });
    }
    if (this.target.classList.contains('tools__draw__circle')) {
      hotkeys('*', (event) => {
        this.keyBoard.keyCircle = `${event.key}`;
        this.keyBoard.keyToolsEvent.bind(this.keyBoard);
        this.keyBoard.keyToolsEvent(this.target);
        this.target.setAttribute('title', `${this.keyBoard.keyCircle}`);
      });
    }
    window.removeEventListener('keyup', this.linkOfchangeKey);
  }
}
