/* eslint-disable guard-for-in */
/* eslint-disable no-restricted-syntax */
export default class Layers {
  constructor(canvas) {
    this.canvasClass = canvas;
    this.canvas = null;
    this.context = null;
    this.layers = {};
    this.layersArray = [];
    this.layerNumber = 0;
    this.numberOfLayer = null;
    this.currentLayer = null;
  }

  beginLayer() {
    const btnNewLayer = document.querySelector('.layers__add_new_layer');
    const btnDeleteLayer = document.querySelector('.layers__delete_layer');
    const btnMoveUpLayer = document.querySelector('.layers__move_up_layer');
    const btnMoveDownLayer = document.querySelector('.layers__move_down_layer');
    const btnDuplicateLayer = document.querySelector('.layers__duplicate_layer');

    btnNewLayer.addEventListener('click', this.addLayer.bind(this));
    btnDeleteLayer.addEventListener('click', this.deleteLayer.bind(this));
    btnMoveUpLayer.addEventListener('click', this.moveUp.bind(this));
    btnMoveDownLayer.addEventListener('click', this.moveDown.bind(this));
    btnDuplicateLayer.addEventListener('click', this.addLayer.bind(this));
  }

  moveUp() {
    for (let i = 0; this.layersArray.length - 1; i += 1) {
      if (this.layersArray[i] === this.currentLayer && this.layersArray[i + 1]) {
        this.currentLayer = this.layersArray[i + 1];
        this.selected();
        return;
      }
    }
  }

  moveDown() {
    for (let i = 0; this.layersArray.length - 1; i += 1) {
      if (this.layersArray[i] === this.currentLayer && this.layersArray[i - 1]) {
        this.currentLayer = this.layersArray[i - 1];
        this.selected();
        return;
      }
    }
  }

  addLayer() {
    this.canvas = document.getElementById('palete');
    this.context = this.canvas.getContext('2d');
    const imageData = this.context.getImageData(0, 0, this.canvasClass.sizeOfCanvas(),
      this.canvasClass.sizeOfCanvas());
    this.numberOfLayer = `Layer ${this.layerNumber += 1}`;
    for (let i = 0; i < imageData.data.length - 1; i += 4) {
      if (imageData.data[i + 3] !== 0) {
        imageData.data[i + 3] = 90;
      }
    }
    this.context.putImageData(imageData, 0, 0);
    this.layers[this.numberOfLayer] = imageData;
    this.layersArray.push(imageData);
    this.currentLayer = this.layers[this.numberOfLayer];
    this.createLayersList();
  }

  createLayersList() {
    const list = document.querySelector('.layers__list');
    const layer = document.createElement('li');
    layer.classList.add('layer__list_item');
    layer.innerHTML = this.numberOfLayer;

    list.appendChild(layer);

    this.eventsForLayers();
  }

  eventsForLayers() {
    const list = document.querySelector('.layers__list');
    list.addEventListener('click', this.selected.bind(this));
  }

  selected(e) {
    if (e) {
      const { target } = e;
      this.nameOfLayer = target;
      const obj = this.layers;
      for (const key in obj) {
        if (target.innerHTML === key) {
          this.currentLayer = obj[key];
          this.select = key;
        }
      }
    }
    this.context.putImageData(this.currentLayer, 0, 0);
  }

  deleteLayer(e) {
    const { target } = e;
    document.querySelector('.layers__list').removeChild(this.nameOfLayer);
    this.layersArray.forEach((curr, i) => {
      if (curr === this.layers[this.select]) {
        this.layersArray.splice(i, 1);
      }
    });
    delete this.layers[target.innerHTML];
  }
}
