export default class Colors {
  constructor() {
    this.result = null;
    this.primary = document.getElementById('primary');
    this.secondary = document.getElementById('secondary');
    this.color = null;
  }

  getRgb(button) {
    if (button === 0) {
      const { value } = this.primary;
      this.hex2rgb(value);
    } else if (button === 2) {
      const { value } = this.secondary;
      this.hex2rgb(value);
    }
    return this.color;
  }

  hex2rgb(hex) {
    this.result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    this.color = this.result ? {
      r: parseInt(this.result[1], 16),
      g: parseInt(this.result[2], 16),
      b: parseInt(this.result[3], 16),
      a: 255,
    } : null;
  }

  componentToHex(c) {
    this.hex = c.toString(16);
    return this.hex.length === 1 ? `0${this.hex}` : this.hex;
  }

  rgbToHex(r, g, b) {
    return `#${this.componentToHex(r)}${this.componentToHex(g)}${this.componentToHex(b)}`;
  }
}
