/* eslint-disable */
export default class Circle {
  constructor(frames, colors, canvas, thickness) {
    this.frames = frames;
    this.colors = colors;
    this.canvasClass = canvas;
    this.thickness = thickness;
  }

  circleBresenhams(x0, y0, x, y, divider, ctx) {
    const penSize = this.thickness.thickness;
    this.penSize = penSize;
    const radius = Math.sqrt((x - x0) ** 2 + (y - y0) ** 2);
    let x1 = 0;
    let y1 = radius;
    let gap = 0;
    let delta = 1 - 2 * radius;

    while (y1 >= 0) {
      ctx.fillRect(
        Math.floor((x0 + x1) / divider) * divider,
        Math.floor((y0 - y1) / divider) * divider,
        penSize,
        penSize,
      );
      ctx.fillRect(
        Math.floor((x0 - x1) / divider) * divider,
        Math.floor((y0 - y1) / divider) * divider,
        penSize,
        penSize,
      );
      ctx.fillRect(
        Math.floor((x0 - x1) / divider) * divider,
        Math.floor((y0 + y1) / divider) * divider,
        penSize,
        penSize,
      );
      ctx.fillRect(
        Math.floor((x0 + x1) / divider) * divider,
        Math.floor((y0 + y1) / divider) * divider,
        penSize,
        penSize,
      );

      gap = 2 * (delta + y1) - 1;
      if (delta < 0 && gap <= 0) {
        x1 += 1;
        delta += 2 * x1 + 1;
      } else if (delta > 0 && gap > 0) {
        y1 -= 1;
        delta -= 2 * y1 + 1;
      } else {
        x1 += 1;
        delta += 2 * (x1 - y1);
        y1 -= 1;
      }
    }
  }

  mainCnavasMouseDown(e) {
    e.preventDefault();
    this.started = true;
    this.ctx = this.frames.getCurrent().getContext('2d');
    this.imageData = this.ctx.getImageData(0, 0, this.canvasClass.sizeOfCanvas(),
      this.canvasClass.sizeOfCanvas());
    this.button = e.button;
    const canvas = e.target;
    this.canvas = canvas;
    this.coords = this.frames.getCoordinats(e.clientX, e.clientY);
    this.mainCnavasMouseMove = this.mainCnavasMouseMove.bind(this);
    this.mainCnavasMouseUp = this.mainCnavasMouseUp.bind(this);
    if (e.button === 1) return;

    if (this.button === 0) {
      this.ctx.fillStyle = this.colors.primary.value;
    } else {
      this.ctx.fillStyle = this.colors.secondary.value;
    }

    this.x0 = Math.floor(e.offsetX / 10);
    this.y0 = Math.floor(e.offsetY / 10);

    this.ctx.moveTo(this.x0, this.y0);
    canvas.addEventListener('mousemove', this.mainCnavasMouseMove);
    canvas.addEventListener('mouseup', this.mainCnavasMouseUp);
  }

  mainCnavasMouseMove(e) {
    if (e.which === 1 || e.which === 3) {
      this.ctx.clearRect(0, 0, this.canvasClass.sizeOfCanvas(), this.canvasClass.sizeOfCanvas());
      if (this.imageData) {
        this.ctx.putImageData(this.imageData, 0, 0);
      }
      this.x1 = Math.floor(e.offsetX / 10);
      this.y1 = Math.floor(e.offsetY / 10);

      this.circleBresenhams(
        this.x0 * 10,
        this.y0 * 10,
        this.x1 * 10,
        this.y1 * 10,
        10,
        this.ctx,
      );
    }
  }

  mainCnavasMouseUp(e) {
    if (this.started) {
      e.preventDefault();
      this.x1 = Math.floor(e.offsetX / 10);
      this.y1 = Math.floor(e.offsetY / 10);

      this.circleBresenhams(
        this.x0 * 10,
        this.y0 * 10,
        this.x1 * 10,
        this.y1 * 10,
        10,
        this.ctx,
      );
      this.imageData = this.ctx.getImageData(0, 0, this.canvasClass.sizeOfCanvas(),
        this.canvasClass.sizeOfCanvas());
      this.started = false;
      this.frames.createFramesList();
      this.canvas.removeEventListener('mouseup', this.mainCnavasMouseUp);
    }
  }
}
