/* eslint-disable */
export default class StrokeLine {
  constructor(frames, colors, canvas, thickness) {
    this.frames = frames;
    this.colors = colors;
    this.canvasClass = canvas;
    this.thickness = thickness;
    this.divider = 10;
  }

  simpleBresenhams(cX0, cY0, cX1, cY1, divider, ctx) {
    const penSize = this.thickness.thickness;
    this.penSize = penSize * divider;
    const dx = Math.abs(cX1 - cX0);
    const dy = Math.abs(cY1 - cY0);
    const sx = cX0 < cX1 ? 1 : -1;
    const sy = cY0 < cY1 ? 1 : -1;
    let err = dx - dy;
    let coordX = null;
    let coordY = null;

    while (true) {
      coordX = cX0;
      coordY = cY0;
      coordX *= divider;
      coordY *= divider;

      ctx.fillRect(coordX, coordY, penSize, penSize);

      if (cX0 === cX1 && cY0 === cY1) {
        break;
      }
      const err2 = 2 * err;
      if (err2 >= -dy) {
        err -= dy;
        cX0 += sx;
      }
      if (err2 <= dx) {
        err += dx;
        cY0 += sy;
      }
    }
  }

  mainCnavasMouseDown(e) {
    e.preventDefault();
    this.started = true;
    this.ctx = this.frames.getCurrent().getContext('2d');
    this.imageData = this.ctx.getImageData(0, 0, this.canvasClass.sizeOfCanvas(),
      this.canvasClass.sizeOfCanvas());
    this.button = e.button;
    const canvas = e.target;
    this.canvas = canvas;
    this.mainCnavasMouseMove = this.mainCnavasMouseMove.bind(this);
    this.mainCnavasMouseUp = this.mainCnavasMouseUp.bind(this);

    this.x0 = Math.floor(e.offsetX / this.divider);
    this.y0 = Math.floor(e.offsetY / this.divider);

    this.ctx.moveTo(this.x0, this.y0);
    canvas.addEventListener('mousemove', this.mainCnavasMouseMove);
    canvas.addEventListener('mouseup', this.mainCnavasMouseUp);
  }

  mainCnavasMouseMove(evt) {
    if (evt.which === 1 || evt.which === 3) {
      this.ctx.clearRect(0, 0, this.canvasClass.sizeOfCanvas(), this.canvasClass.sizeOfCanvas());
      if (this.imageData) {
        this.ctx.putImageData(this.imageData, 0, 0);
      }
      if (this.button === 0) {
        this.ctx.fillStyle = this.colors.primary.value;
      } else {
        this.ctx.fillStyle = this.colors.secondary.value;
      }
      this.x1 = Math.floor(evt.offsetX / this.divider);
      this.y1 = Math.floor(evt.offsetY / this.divider);
      this.simpleBresenhams(this.x0, this.y0, this.x1, this.y1, this.divider, this.ctx);
    }
  }

  mainCnavasMouseUp(event) {
    if (this.started) {
      event.preventDefault();
      this.x1 = Math.floor(event.offsetX / this.divider);
      this.y1 = Math.floor(event.offsetY / this.divider);
      this.simpleBresenhams(this.x0, this.y0, this.x1, this.y1, this.divider, this.ctx);
      this.imageData = this.ctx.getImageData(0, 0, this.canvasClass.sizeOfCanvas(),
        this.canvasClass.sizeOfCanvas());
      this.started = false;
      this.canvas.removeEventListener('mousemove', this.mainCnavasMouseMove);
      this.canvas.removeEventListener('mouseup', this.mainCnavasMouseUp);
      this.frames.createFramesList();
    }
  }
}
