import hotkeys from 'hotkeys-js';

export default class KeyBoardShortcuts {
  constructor(eventForTools, thickness) {
    this.eventForTools = eventForTools;
    this.thickness = thickness;
    this.keyPen = 'A';
    this.keyBucket = 'B';
    this.keyEraiser = 'E';
    this.keyRectangle = 'R';
    this.keyMirror = 'V';
    this.keyLighten = 'L';
    this.keyColorPicker = 'P';
    this.keyDithering = 'D';
    this.keyPaint = 'N';
    this.keyStroke = 'S';
    this.keyMove = 'M';
    this.keyShape = 'I';
    this.keyRotate = 'J';
    this.keyFlip = 'F';
    this.keyCircle = 'C';
    this.keyDecreaseSize = '[';
    this.keyIncreaseSize = ']';
    this.pen = document.querySelector('.tools__draw__pen');
    this.bucket = document.querySelector('.tools__draw__bucket');
    this.eraiser = document.querySelector('.tools__draw__erasier');
    this.rectangle = document.querySelector('.tools__draw__rectangle');
    this.mirror = document.querySelector('.tools__draw__mirror_pen');
    this.lighten = document.querySelector('.tools__draw__lighten');
    this.picker = document.querySelector('.tools__draw__color_picker');
    this.dithering = document.querySelector('.tools__draw__dithering');
    this.paint = document.querySelector('.tools__draw__paint_all_pixels');
    this.stroke = document.querySelector('.tools__draw__stroke_line');
    this.move = document.querySelector('.tools__draw__move_tool');
    this.shape = document.querySelector('.tools__draw__shape_tool');
    this.rotate = document.querySelector('.tools__draw__rotate_tool');
    this.circle = document.querySelector('.tools__draw__circle');
    this.flip = document.querySelector('.tools__draw__flip_tool');
  }

  keyToolsEvent() {
    hotkeys(`${this.keyPen}`, () => {
      const e = {
        target: this.pen,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.pen.setAttribute('title', `${this.keyPen}`);
    hotkeys(`${this.keyBucket}`, () => {
      const e = {
        target: this.bucket,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.bucket.setAttribute('title', `${this.keyBucket}`);
    hotkeys(`${this.keyEraiser}`, () => {
      const e = {
        target: this.eraiser,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.eraiser.setAttribute('title', `${this.keyEraiser}`);
    hotkeys(`${this.keyRectangle}`, () => {
      const e = {
        target: this.rectangle,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.rectangle.setAttribute('title', `${this.keyRectangle}`);
    hotkeys(`${this.keyMirror}`, () => {
      const e = {
        target: this.mirror,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.mirror.setAttribute('title', `${this.keyMirror}`);
    hotkeys(`${this.keyLighten}`, () => {
      const e = {
        target: this.lighten,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.lighten.setAttribute('title', `${this.keyLighten}`);
    hotkeys(`${this.keyColorPicker}`, () => {
      const e = {
        target: this.picker,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.picker.setAttribute('title', `${this.keyColorPicker}`);
    hotkeys(`${this.keyDithering}`, () => {
      const e = {
        target: this.dithering,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.dithering.setAttribute('title', `${this.keyDithering}`);
    hotkeys(`${this.keyPaint}`, () => {
      const e = {
        target: this.paint,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.paint.setAttribute('title', `${this.keyPaint}`);
    hotkeys(`${this.keyStroke}`, () => {
      const e = {
        target: this.stroke,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.stroke.setAttribute('title', `${this.keyStroke}`);
    hotkeys(`${this.keyMove}`, () => {
      const e = {
        target: this.move,
      };
      this.eventForTools.beginToolsEvent(e);
    });

    this.move.setAttribute('title', `${this.keyMove}`);
    hotkeys(`${this.keyShape}`, () => {
      const e = {
        target: this.shape,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.shape.setAttribute('title', `${this.keyShape}`);

    hotkeys(`${this.rotate}`, () => {
      const e = {
        target: this.rotate,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.rotate.setAttribute('title', `${this.keyRotate}`);

    hotkeys(`${this.keyFlip}`, () => {
      const e = {
        target: this.flip,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.flip.setAttribute('title', `${this.keyFlip}`);

    hotkeys(`${this.keyCircle}`, () => {
      const e = {
        target: this.circle,
      };
      this.eventForTools.beginToolsEvent(e);
    });
    this.circle.setAttribute('title', `${this.keyCircle}`);

    hotkeys(`${this.keyDecreaseSize}`, () => {
      if (this.thickness.thickness > 1) {
        const e = {
          target: document.querySelector(`.thickness-${(this.thickness.thickness / 10) - 1}`),
        };
        this.thickness.getBushThickness(e);
      }
    });
    hotkeys(`${this.keyIncreaseSize}`, () => {
      if (this.thickness.thickness > 4) {
        const e = {
          target: document.querySelector(`.thickness-${(this.thickness.thickness / 10) + 1}`),
        };
        this.thickness.getBushThickness(e);
      }
    });
  }
}
