/* eslint-disable no-useless-escape */
import $ from 'jquery';

export default class OAUTH {
  constructor() {
    this.oauthUrl = 'https://accounts.google.com/o/oauth2/auth?';
    this.validUrl = 'https://www.googleapis.com/oauth2/v1/tokeninfo?access_token=';
    this.scope = 'https://www.googleapis.com/auth/userinfo.profile';
    this.clientId = '151269531141-giqgb4vdgcskph7r7fprlrlss71n4lal.apps.googleusercontent.com';
    this.redirect = 'http://localhost:8080/MAMP/html5/oauth/';
    this.type = 'token';
    this.URL = null;
    this.url = null;
    this.acToken = null;
    this.tokenType = null;
    this.expiresIn = null;
    this.name = null;
    this.loggedIn = false;
  }

  login() {
    this.URL = `${this.oauthUrl}scope=${this.scope}&client_id=${this.clientId}&redirect_uri=${this.redirect}&response_type=${this.type}`;
    const win = window.open(this.URL, 'windowname1', 'width=800', 'height=600');
    const pollTimer = window.setInterval(() => {
      try {
        console.log(win.document.URL);
        if (win.document.URL.indexOf(this.redirect) !== -1) {
          window.clearInterval(pollTimer);
          this.url = win.document.URL;
          this.acToken = this.gup(this.url, 'access_token');
          this.tokenType = this.gup(this.url, 'token_type');
          this.expiresIn = this.gup(this.url, 'expires_in');
          win.close();

          this.validateToken();
        }
      } catch (e) {
        console.log(e);
      }
    }, 500);
  }

  gup(url, name) {
    this.name = name.replace(/[\[]/, '\\\[').replace(/[\]]/, '\\\]');
    const regexS = `[\\#&]${this.name}=([^&#]*)`;
    const regex = new RegExp(regexS);
    const results = regex.exec(url);
    if (results == null) return '';
    return results[1];
  }

  validateToken() {
    $.ajax({
      url: this.validUrl + this.acToken,
      data: null,
      success: () => {
        this.getUserInfo();
        this.loggedIn = true;
        $('#loginText').hide();
        $('#logoutText').show();
        $('#logoutText').css({ display: 'flex' });
      },
      dataType: 'jsonp',
    });
  }

  getUserInfo() {
    $.ajax({
      url: `https://www.googleapis.com/oauth2/v1/userinfo?access_token=${this.acToken}`,
      data: null,
      success: (resp) => {
        $('.container__name_and_photo').css({ display: 'flex' });
        const user = resp;
        $('#uName').text('Welcome, ').append(user.name);
        $('#imgHolder').attr('src', user.picture);
      },
      dataType: 'jsonp',
    });
  }

  startLogOutPolling() {
    $('#loginText').show();
    $('#logoutText').hide();
    this.loggedIn = false;
    $('#uName').text('Welcome ');
    $('#imgHolder').attr('src', 'none.jpg');
    $('.container__name_and_photo').css({ display: 'none' });
  }
}
