import GIFEncoder from 'gifencoder';
import { createCanvas } from 'canvas';

export default class Gif {
  constructor(frames, canvas, preview) {
    this.preview = preview;
    this.canvasClass = canvas;
    this.frames = frames;
    this.width = 320;
    this.height = 320;
    this.baseURL = 'data:image/gif;base64,';
    this.gif = null;
  }

  beginMakeGif() {
    const encoder = new GIFEncoder(320, 320);
    encoder.start();
    encoder.setRepeat(0);
    encoder.setDelay(this.preview.getFPS);
    encoder.setQuality(10);

    this.canvas = createCanvas(320, 320);
    const ctx = this.canvas.getContext('2d');

    for (let i = 0; i < this.frames.framesArray.length; i += 1) {
      const imageData = this.frames.framesArray[i].getContext('2d').getImageData(0, 0, this.canvasClass.sizeOfCanvas(),
        this.canvasClass.sizeOfCanvas());
      ctx.putImageData(imageData, 0, 0);
      encoder.addFrame(ctx);
    }

    encoder.finish();

    const buf = encoder.out.getData();
    this.download(buf);
  }

  download(buf) {
    this.link = document.createElement('a');
    this.link.download = 'download.gif';
    this.data = URL.createObjectURL(new Blob([new Uint8Array(buf)], { type: 'image/gif' }));
    this.link.href = this.data;
    this.link.click();
  }
}
