export default class EventsForTools {
  constructor(penTool, bucket, rectangle, mirrorPen, lighten, colorPicker,
    dithering, paintAllpixels, strokeLine, moveTool, shapeSlection, rotate, flip, circle) {
    this.circle = circle;
    this.flip = flip;
    this.rotate = rotate;
    this.shapeSlection = shapeSlection;
    this.moveTool = moveTool;
    this.strokeLine = strokeLine;
    this.paintAllpixels = paintAllpixels;
    this.dithering = dithering;
    this.colorPicker = colorPicker;
    this.lighten = lighten;
    this.mirrorPen = mirrorPen;
    this.penTool = penTool;
    this.bucket = bucket;
    this.rectangle = rectangle;
  }

  beginToolsEvent(e) {
    const { target } = e;
    target.classList.add('select');
    const canvas = document.querySelector('.main__canvas');
    this.removesEvents = this.removesEvents.bind(this);
    this.removesEvents(canvas);
    this.prevTool = target;
    if (target.classList.contains('tools__draw__pen')) {
      this.linkOfPen = this.eventPen.bind(this);
      canvas.addEventListener('mousedown', this.linkOfPen);
    }
    if (target.classList.contains('tools__draw__bucket')) {
      this.linkOfBucket = this.eventBucket.bind(this);
      canvas.addEventListener('mousedown', this.linkOfBucket);
    }
    if (target.classList.contains('tools__draw__erasier')) {
      this.linkOfEraiser = this.eventEraiser.bind(this);
      canvas.addEventListener('mousedown', this.linkOfEraiser);
    }
    if (target.classList.contains('tools__draw__rectangle')) {
      this.linkOfRect = this.eventRectangle.bind(this);
      canvas.addEventListener('mousedown', this.linkOfRect);
    }
    if (target.classList.contains('tools__draw__mirror_pen')) {
      this.linkOfMirrorPen = this.eventMirrorPen.bind(this);
      canvas.addEventListener('mousedown', this.linkOfMirrorPen);
    }
    if (target.classList.contains('tools__draw__lighten')) {
      this.linkOfLighten = this.eventLighten.bind(this);
      canvas.addEventListener('mousedown', this.linkOfLighten);
    }
    if (target.classList.contains('tools__draw__color_picker')) {
      this.linkOfColorPicker = this.eventColorPicker.bind(this);
      canvas.addEventListener('mousedown', this.linkOfColorPicker);
    }
    if (target.classList.contains('tools__draw__dithering')) {
      this.linkOfDithering = this.eventDithering.bind(this);
      canvas.addEventListener('mousedown', this.linkOfDithering);
    }
    if (target.classList.contains('tools__draw__paint_all_pixels')) {
      this.linkOfPaintAllPixels = this.eventPaintAllPixels.bind(this);
      canvas.addEventListener('mousedown', this.linkOfPaintAllPixels);
    }
    if (target.classList.contains('tools__draw__stroke_line')) {
      this.linkOfStrokeLine = this.eventStrokeLine.bind(this);
      canvas.addEventListener('mousedown', this.linkOfStrokeLine);
    }
    if (target.classList.contains('tools__draw__move_tool')) {
      this.linkOfMoveTool = this.eventMoveTool.bind(this);
      canvas.addEventListener('mousedown', this.linkOfMoveTool);
    }
    if (target.classList.contains('tools__draw__shape_tool')) {
      this.linkOfShapeSelection = this.eventShapeSelection.bind(this);
      canvas.addEventListener('mousedown', this.linkOfShapeSelection);
    }
    if (target.classList.contains('tools__draw__rotate_tool')) {
      this.rotate.rotate();
    }
    if (target.classList.contains('tools__draw__flip_tool')) {
      this.flip.flipVerticaly();
    }
    if (target.classList.contains('tools__draw__circle')) {
      this.linkOfCircle = this.eventCircle.bind(this);
      canvas.addEventListener('mousedown', this.linkOfCircle);
    }
  }

  eventCircle(e) {
    this.circle.mainCnavasMouseDown = this.circle.mainCnavasMouseDown.bind(this.circle);
    this.circle.mainCnavasMouseDown(e);
  }

  eventShapeSelection(e) {
    this.shapeSlection.beginSelect = this.shapeSlection.beginSelect.bind(this.shapeSlection);
    this.shapeSlection.beginSelect(e);
  }

  eventMoveTool(e) {
    this.moveTool.mainCnavasMouseDown = this.moveTool.mainCnavasMouseDown.bind(this.moveTool);
    this.moveTool.mainCnavasMouseDown(e);
  }

  eventStrokeLine(e) {
    this.strokeLine.mainCnavasMouseDown = this.strokeLine.mainCnavasMouseDown.bind(this.strokeLine);
    this.strokeLine.mainCnavasMouseDown(e);
  }

  eventPaintAllPixels(e) {
    this.paintAllpixels.beginPaint = this.paintAllpixels.beginPaint.bind(this.paintAllpixels);
    this.paintAllpixels.beginPaint(e);
  }

  eventDithering(e) {
    this.dithering.mainCnavasMouseDown = this.dithering.mainCnavasMouseDown.bind(this.dithering);
    this.dithering.mainCnavasMouseDown(e);
  }

  eventColorPicker(e) {
    this.colorPicker.getColor = this.colorPicker.getColor
      .bind(this.colorPicker);
    this.colorPicker.getColor(e);
  }

  eventLighten(e) {
    this.lighten.mainCnavasMouseDown = this.lighten.mainCnavasMouseDown.bind(this.lighten);
    this.lighten.mainCnavasMouseDown(e);
  }

  eventMirrorPen(e) {
    this.mirrorPen.mainCnavasMouseDown = this.mirrorPen.mainCnavasMouseDown.bind(this.mirrorPen);
    this.mirrorPen.mainCnavasMouseDown(e);
  }

  removesEvents(canvas) {
    if (this.prevTool) {
      this.prevTool.classList.remove('select');
    }
    canvas.removeEventListener('mousedown', this.linkOfPen);
    canvas.removeEventListener('mousedown', this.linkOfBucket);
    canvas.removeEventListener('mousedown', this.linkOfEraiser);
    canvas.removeEventListener('mousedown', this.linkOfRect);
    canvas.removeEventListener('mousedown', this.linkOfMirrorPen);
    canvas.removeEventListener('mousedown', this.linkOfLighten);
    canvas.removeEventListener('mousedown', this.linkOfColorPicker);
    canvas.removeEventListener('mousedown', this.linkOfDithering);
    canvas.removeEventListener('mousedown', this.linkOfPaintAllPixels);
    canvas.removeEventListener('mousedown', this.linkOfStrokeLine);
    canvas.removeEventListener('mousedown', this.linkOfMoveTool);
    canvas.removeEventListener('mousedown', this.linkOfShapeSelection);
    canvas.removeEventListener('mousedown', this.linkOfCircle);
  }

  eventRectangle(e) {
    this.rectangle.mainCnavasMouseDown = this.rectangle.mainCnavasMouseDown.bind(this.rectangle);
    this.rectangle.mainCnavasMouseDown(e);
  }

  eventPen(e) {
    this.penTool.mainCnavasMouseDown = this.penTool.mainCnavasMouseDown.bind(this.penTool);
    this.penTool.mainCnavasMouseDown(e);
  }

  eventBucket(e) {
    this.bucket.beginBucket = this.bucket.beginBucket.bind(this.bucket);
    this.bucket.beginBucket(e);
  }

  eventEraiser(e) {
    this.penTool.mainCnavasMouseDown = this.penTool.mainCnavasMouseDown.bind(this.penTool);
    this.penTool.mainCnavasMouseDown(e, 90);
  }
}
