import PenTool from './pen';

export default class Dithering extends PenTool {
  draw(x, y) {
    if (x < 0 || y < 0 || x > this.canvasClass.sizeOfCanvas()
      || y > this.canvasClass.sizeOfCanvas()) return;
    this.ctx = this.frames.getCurrent().getContext('2d');
    this.colorPrimary = this.colors.getRgb(0);
    this.colorSecondary = this.colors.getRgb(2);
    if (this.button === 0) {
      if ((Math.ceil(x / 10) + Math.ceil(y / 10)) % 2 === 0) {
        this.drawPixels(this.colorPrimary);
      } else {
        this.drawPixels(this.colorSecondary);
      }
    } else if ((Math.ceil(x / 10) + Math.ceil(y / 10)) % 2 === 0) {
      this.drawPixels(this.colorSecondary);
    } else {
      this.drawPixels(this.colorPrimary);
    }
    this.ctx.putImageData(this.imageDataDithering, x - (x % this.pixel),
      y - (y % this.pixel));
    this.frames.createFramesList();
  }

  drawPixels(color) {
    for (let i = 0; i < this.canvasClass.sizeOfCanvas(); i += 10) {
      for (let j = 0; j < this.canvasClass.sizeOfCanvas(); j += 10) {
        this.imageDataDithering = this.ctx.createImageData(10,
          10);
        for (let k = 0; k < this.imageDataDithering.data.length; k += 4) {
          this.imageDataDithering.data[k + 0] = color.r;
          this.imageDataDithering.data[k + 1] = color.g;
          this.imageDataDithering.data[k + 2] = color.b;
          this.imageDataDithering.data[k + 3] = 255;
        }
      }
    }
  }
}
