export default class Canvas {
  constructor() {
    this.size = null;
  }

  redrawCanvasForSize() {
    const input = document.getElementById('sizeForCanvas');
    const { value } = input;
    this.size = value * 10;
  }

  sizeOfCanvas() {
    const input = document.getElementById('sizeForCanvas');
    const { value } = input;
    this.size = value * 10;
    if (this.size > 1000) {
      document.querySelector('.main__canvas').style.overflow = 'scroll';
      document.querySelector('.main__canvas').style.height = `${this.size}`;
    }
    return this.size;
  }
}
