import $ from 'jquery';

export default class Png {
  constructor() {
    this.canvas = null;
  }

  getLinlToDownload() {
    this.canvas = null;
    $('.export__png').on('click', () => {
      const link = document.createElement('a');
      link.setAttribute('href', `${this.getDataUrl()}`);
      link.setAttribute('download', 'download.png');
      link.click();
      return false;
    });
  }

  getDataUrl() {
    this.canvas = document.getElementById('palete');
    return this.canvas.toDataURL('image/png');
  }
}
