import UPNG from 'upng-js';

export default class APNG {
  constructor(frames) {
    this.frames = frames;
    this.dta = [];
  }

  beginMakeApng() {
    const ctx = this.frames.framesArray[0].getContext('2d');
    const ctx2 = this.frames.framesArray[1].getContext('2d');
    this.dta.push(ctx.getImageData(0, 0, 320, 320).data.buffer);
    this.dta.push(ctx2.getImageData(0, 0, 320, 320).data.buffer);
    const buf = UPNG.encode(this.dta, 320, 320, 0, [500, 500]);
    this.download(buf);
  }

  download(buf) {
    this.link = document.createElement('a');
    this.link.download = 'download.apng';
    this.link.href = URL.createObjectURL(new Blob([new Uint8Array(buf)], { type: 'image/apng' }));
    this.link.click();
  }
}
