export default class Coordinats {
  constructor() {
    this.x = document.getElementById('x');
    this.y = document.getElementById('y');
    this.canvas = null;
  }

  beginShowCoordinats() {
    this.canvas = document.getElementById('palete');
    this.canvas.addEventListener('mousemove', this.moveMouse.bind(this));
    this.canvas.addEventListener('mouseout', () => {
      this.showCoordinats(0, 0);
    });
  }

  getCoordinats(x, y) {
    const canvasRect = this.canvas.getBoundingClientRect();
    return {
      x: x - canvasRect.left,
      y: y - canvasRect.top,
    };
  }

  moveMouse(e) {
    const coordsMove = this.getCoordinats(e.clientX, e.clientY);
    this.showCoordinats(coordsMove.x, coordsMove.y);
  }

  showCoordinats(x, y) {
    this.x.innerHTML = `X: ${Math.ceil(x / 10)}`;
    this.y.innerHTML = `Y: ${Math.ceil(y / 10)}`;
  }
}
