/* eslint-disable prefer-destructuring */
export default class PaintAllPixelsOfSameColor {
  constructor(frames, colors, canvas) {
    this.frames = frames;
    this.colors = colors;
    this.canvasClass = canvas;
    this.pixel = 10;
  }

  beginPaint(e) {
    this.coord = this.frames.getCoordinats(e.clientX, e.clientY);
    this.colorForPaint = this.colors.getRgb(e.button);
    this.paint(this.coord.x, this.coord.y);
  }

  paint(x, y) {
    if (x < 0 || y < 0 || x > this.canvasClass.sizeOfCanvas()
      || y > this.canvasClass.sizeOfCanvas()) return;
    this.ctx = this.frames.getCurrent().getContext('2d');
    this.imageData = this.ctx.getImageData(x, y, 10, 10);
    this.color = {};
    this.color.r = this.imageData.data[0];
    this.color.g = this.imageData.data[1];
    this.color.b = this.imageData.data[2];
    this.color.a = this.imageData.data[3];
    const canvasImageData = this.ctx.getImageData(0, 0, this.canvasClass.sizeOfCanvas(),
      this.canvasClass.sizeOfCanvas());
    for (let k = 0; k < canvasImageData.data.length; k += 4) {
      if (this.color.r === canvasImageData.data[k + 0]
        && this.color.g === canvasImageData.data[k + 1]
        && this.color.b === canvasImageData.data[k + 2]
        && this.color.a === canvasImageData.data[k + 3]) {
        canvasImageData.data[k + 0] = this.colorForPaint.r;
        canvasImageData.data[k + 1] = this.colorForPaint.g;
        canvasImageData.data[k + 2] = this.colorForPaint.b;
        canvasImageData.data[k + 3] = 255;
      }
    }
    this.ctx.putImageData(canvasImageData, 0, 0);
    this.frames.createFramesList();
  }
}
